section {
  max-width: 1300px;
  margin: auto;
  padding: 20px;
  background: #FFD800;
}

@media (max-width: 1515px) {
  section {
    max-width: 100%;
  } 
}

section .logo {
  transform: translateX(-87px);
}

section a {
  float: right;
  padding: 3px 0;
}

* {
  outline: none;
}

.wrapper {
  max-width: 1335px;
  box-sizing: border-box;
  padding: 0 20px;
  margin: auto;
}

.wrapper a {
  display: inline-block;
  margin: 5px;
}

.wrapper a:first-child {
  margin-left: 0;
}

.slider-wrapper {
  position: relative;
  height: calc(100vh - 100px);
  border-radius: 15px;
  overflow: hidden;
}

.slide {
  margin: 0 10px;
  width: calc(100% - 20px);
  height: calc(100vh - 100px);
  border-radius: 15px;
  overflow: hidden;
  background-size: cover !important;
}

.slide::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background: -webkit-gradient(180deg, rgba(16, 7, 0, 0.72) 36.73%, rgba(31, 14, 3, 0.1152) 124.33%); 
    background: linear-gradient(180deg, rgba(16, 7, 0, 0.72) 36.73%, rgba(31, 14, 3, 0.1152) 124.33%); 
    bottom: 0;
    left: 0;
}

.previousButton {
  display: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: all ease .15s;
  z-index: 800;
  background: url('../../svg/arrowLeft.svg') no-repeat center center / 30px;
  width: 35px;
  height: 35px;
  text-indent: -9999px;
  cursor: pointer;
}
.nextButton {
  display: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: all ease .15s;
  z-index: 800;
  background: url('../../svg/arrowRight.svg') no-repeat center center / 30px;
  width: 35px;
  height: 35px;
  text-indent: -9999px;
  cursor: pointer;
}

.previousButton {
  left: 10px;
}

.previousButton:hover {
  left: 10px;
}

.nextButton {
  right: 10px;
}

.nextButton:hover {
  right: 10px;
}

.slider-content {
  text-align: center;
}

.slider-content .inner {
  padding: 0 70px;
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.slider-content .inner button{
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}

.slider-content .inner h2 {
  margin: 0 auto;
  max-width: 840px;
  color: #FFFFFF;
}

.slider-content .inner h3 {
  color: #FFFFFF;
  margin: 20px auto 30px;
  max-width: 740px;
}

.slider-content section {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.slider-content section span {
  color: #FFFFFF;
}

.slider-content section span {
  color: rgba(255, 255, 255, 0.5);
  display: inline-block;
  text-align: left;
  vertical-align: middle;
  margin-left: 10px;
}

.slider-content section img {
  width: 40px;
  height: 40px;
  border: solid 2px rgba(255, 255, 255, 0.5);
  border-radius: 100%;
  vertical-align: middle;
}

.slider-content section span strong {
  color: #FFFFFF;
  display: block;
}

@media (min-width: 800px) {
  .slider-wrapper,.slide {
    height: 480px;
  }
}
